  margin-bottom: 15px;
}

.recommended-cards-group .recommended-cards-title {
  margin-bottom: 45px;
}

.recommended-cards-group .recommended-cards-title h1 {
  font-size: 26px;
  font-weight: bold;
  color: #212121;
}

.recommended-cards-group .recommended-card {
  padding: 20px 10px 20px 20px;
}

.recommended-cards-group .recommended-card .custom-card-image img {
  width: 100%;
