  margin-top: 15px;
}

/* category */
.category h1 {
  margin-bottom: 30px;
}

.category h3 {
  margin-bottom: 20px;
}

.category ul li {
  margin-bottom: 15px;
}

.category .recommended-cards-group {
  margin: 35px 0px 45px 0px;
}

.custom-accordion .card {
  border: none;
  border-bottom: 2px solid #ccc;
}

.custom-accordion .card .card-header {
  padding: 0px;
  border-bottom: none;
  background: #ffffff;
}

.custom-accordion .card .card-header .btn-link {
  color: #09243f;
  font-size: 18px;
  font-weight: 300;
  line-height: 30px;
  text-align: left;
  display: block;
  width: 100%;
  position: relative;
  padding-left: 0px;
  text-decoration: none;
  box-shadow: none !important;
}

.custom-accordion .card .card-header .btn-link .toggle-icon {
  position: absolute;
  top: 0px;
  right: 0px;
  display: inline-block;
  line-height: 44px;
  width: 44px;
  height: 44px;
  font-size: 17px;
  text-align: center;
  transition: 0.3s;
}

.custom-accordion .card .card-header .btn-link .toggle-icon.fa::before {
  content: "\f078";
}

.custom-accordion .card .card-header .btn-link[aria-expanded="true"] .toggle-icon.fa::before {
  content: "\f077";
}

.custom-accordion .card .card-header .btn-link:hover {
  color: #2764b6;
}

/* Responsive */
@media only screen and (max-width: 768px) {
  /* index.html */
  .cards-links-group a {
    margin-bottom: 10px;
  }
  .newspaper-section span {
    width: 100%;
    text-align: center;
  }
  .newspaper-section img {
    max-width: 40%;
    max-height: 22px;
    margin: 20px;
  }
  .recommended-cards-group .recommended-card .custom-card-image img {
    width: 84%;
    margin-left: 8%;
  }
  .recommended-cards-group .recommended-card .custom-card-info {
    margin-left: 0px;
    text-align: center;
  }
  .recommended-cards-group .recommended-card .custom-card-info .card-features {
    border: none;
  }
  .recommended-cards-group .recommended-card .custom-card-info .card-features .feature {
    border: none;
    padding: 0;
    text-align: left;
  }
  .recommended-cards-group .recommended-card .custom-card-info .card-features .feature p {
    margin-top: 5px;
    font-weight: normal;
  }
  .recommended-cards-group .recommended-card .custom-card-info .card-features .feature:not(:last-child) {
    margin-bottom: 15px;
  }
  .credit-card-profile .container.card-info {
    padding: 45px 15px;
  }
  .card-features .col-6.feature:nth-child(even) {
    border-width: 0px 0px 1px 0px;
  }
}

@media only screen and (min-width: 992px) {
  /* About us Page */
  .about-us p {
    max-width: 60%;
    font-size: 16px;
    font-weight: 300;
  }
  /* faq Page */
  .custom-accordion {
    max-width: 70%;
  }
}

